<template>
  <v-treeview
    class="black--text"
    dense
    hoverable
    :open="initiallyOpen"
    :items="items"
    activatable
    item-key="name"
    open-on-click
  >

    <template v-slot:prepend="{ item, open }">
      <v-icon v-if="!item.file">
        {{ open ? "mdi-folder-open" : "mdi-folder" }}
      </v-icon>
      <v-icon v-else>
        {{ files[item.file] }}
      </v-icon>
    </template>

    <template slot="label" slot-scope="{ item }">
      <div @click="fileSelect(item)">{{ item.name }}</div>
    </template>

  </v-treeview>
</template>

<script>
import store from "../store";
export default {
  computed: {
    items() {
      return this.$store.state.repo ? this.$store.state.repo.items : null;
    }
  },
  store,
  data: () => ({
    initiallyOpen: [""],
    files: {
      html: "mdi-language-html5",
      js: "mdi-nodejs",
      json: "mdi-code-json",
      md: "mdi-language-markdown",
      pdf: "mdi-file-pdf",
      png: "mdi-file-image",
      txt: "mdi-file-document-outline",
      xls: "mdi-file-excel",
      xml: "mdi-file-document-outline"
    }
  }),
  methods: {
    fileSelect: function(item) {
      if (item.path) {
        store.state.selectedFile = item.path;
        console.log(store.state.selectedFile)
      }
    }
  }
};
</script>
