/* eslint-disable */
import store from "../store";
export default class GitHubRepo {
   items = []
   constructor(config, tree) {
      this.config = config;
      this.tree = tree;
      const terminalPathsObjs = tree.filter(obj => obj.path.endsWith('.xml'));
      terminalPathsObjs.forEach(obj => this.createItems(this.items, obj, obj.path));
   }

   get metadataURL() {
      console.log('STORE:')
      console.log(store)
      const baseURL = store.state.dev ? 'https://encludeltd.github.io/easygeocoder' : '..';
      return `${baseURL}/force-app/main/default/`;
   }

   createItems(items, obj, fullPath) {
      let subsequentPaths = obj.path.split('/');
      let thisPath = subsequentPaths.shift();
      let prevObj = items.find(item => item.name === thisPath);
      let item = {};
      if (prevObj) {
         obj.path = subsequentPaths.join("/");
         this.createItems(prevObj.children, obj, fullPath);
         return;
      } 
      if (!subsequentPaths || ! subsequentPaths.length) {
         let fileNameAndExtensions = thisPath.split(".")
         item.name = fileNameAndExtensions.shift();
         item.file = fileNameAndExtensions.slice(-1).pop();
         item.path = fullPath;
         items.push(item)
      } else {
         item.name = thisPath;
         item.children = [];
         items.push(item);
      }
   }

   static async fromConfig(config) {
      const topURL = `https://api.github.com/repos/${config.username}/${config.repo}/git/trees/${config.branch}?recursive=1`;
      const topRes = await fetch(topURL);
      const topJSON = await topRes.json();
      const defaultUrl = topJSON.tree.find(child => child.path === 'force-app/main/default').url;
      const defaultRes = await fetch(defaultUrl + '?recursive=1');
      const defaultJSON = await defaultRes.json();
      return new GitHubRepo(config, defaultJSON.tree);
   }
}